.sub_main{
    padding: 40px 40px 80px;
    box-sizing: border-box;
}

.menu_notification_wrap h2{
    margin-bottom: 30px;
    font-size: 32px;
    color: #fefefe;
}

.menu_notification_wrap .notification_cont{
    background: #fefefe;
    height: auto;
    border-radius: 30px;
    padding: 40px;
    box-sizing: border-box;
}

.notification_cont>div{
    border-bottom: 1px solid #222222;
    padding: 40px;
    box-sizing: border-box;
    display: flex;
    align-items: center;
    position: relative;
}

.notification_cont>div>p{
    font-size: 24px;
}

.time_ago{
    margin-left: 20px;
}

.notification_cont>div:last-child{
    border-bottom: none;
}

.notification_cont>div>span{
    background: #D9D9D9;
    width: 105px;
    height: 105px;
    border-radius: 50px;
    margin-right: 20px;
}

.notification_cont .more_btn{
    position: absolute;
    right: 0;
    width: 40px;
}

.notification_cont .more_btn img{
    width: 40px;
}

.req_bold{
    font-weight: bold;
}

.time_ago{
    color: #555555;
}

.notification_cont p{
    color: #000;
}

.dsp_none.none{
    display: none;
}

.dsp_none.block{
    display: block;
}

@media screen and (max-width:767px){
    .menu_notification_wrap h2{
        font-size: 24px;
    }

    .menu_notification_wrap .notification_cont{
        padding: 20px 40px;
    }

    .notification_cont>div{
        padding: 20px 0;
    }

    .notification_cont>div>p{
        font-size: 16px;
        width: 156px;
    }

    .notification_cont>div>span{
        width: 70px;
        height: 70px;
    }

    .nickname{
        font-size: 16px !important;
    }

    .time_ago{
        margin-left: 6px;
    }
}